import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import moment                                       from 'moment';









/* ################################################### */
/*          helper         */
/* ################################################### */

import { DateUtil }                                 from '../../../../_util/util.date';








/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';






import './s.company.step1.css';




export default class ComponentCompanyStep1 extends ComponentAbstract {



    static propTypes = {
        form                : PropTypes.object
        , formValidation    : PropTypes.object

        , data              : PropTypes.object

        , result            : PropTypes.object

        , onChangeForm      : PropTypes.func

        , onClickSearch     : PropTypes.func

        , onClickDetail     : PropTypes.func
    }


    static defaultProps = {
        form                : {}
        , formValidation    : {}
        , data              : {}

        , result            : {}

        , onChangeForm      : () => {}

        , onClickSearch     : () => {}

        , onClickDetail     : () => {}
    }







    constructor( props ) {
        super( props )

    }











    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {

        return (
            <div>

                <div>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > CIF Number </Col>

                            <Col md={ 9 }>
                                <TextField
                                            underlineStyle={ { borderColor: '#616161' } }
                                            underlineFocusStyle={ { borderColor: '#000' } }
                                            hintText={ "CIF Number" }
                                            value={ this.props.form.cifNumber }
                                            onChange={ ( event , newValue ) => {
                                                this.props.onChangeForm( 'cifNumber' , newValue );
                                            } } />
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > Business Name </Col>

                            <Col md={ 9 }>
                                <TextField
                                            underlineStyle={ { borderColor: '#616161' } }
                                            underlineFocusStyle={ { borderColor: '#000' } }
                                            hintText={ "Business Name" }
                                            value={ this.props.form.bussName }
                                            onChange={ ( event , newValue ) => {
                                                this.props.onChangeForm( 'bussName' , newValue );
                                            } } />
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > Business Registration Number </Col>

                            <Col md={ 9 }>
                                <TextField
                                            underlineStyle={ { borderColor: '#616161' } }
                                            underlineFocusStyle={ { borderColor: '#000' } }
                                            hintText={ "Business Registration Number" }
                                            value={ this.props.form.bussRegNumber }
                                            onChange={ ( event , newValue ) => {
                                                this.props.onChangeForm( 'bussRegNumber' , newValue );
                                            } } />
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > SME Type </Col>

                            <Col md={ 9 }>
                                <SelectField
                                                floatingLabelText={ "SME Type" }
                                                value={ this.props.form.smeType }
                                                onChange={ ( event, value , payload ) => {
                                                    this.props.onChangeForm( 'smeType' , payload );
                                                } }  >
                                    {
                                        this.props.data.smeType.map(
                                            ( item , index ) => {
                                                return (
                                                    <MenuItem
                                                                key={ '_company_smeType_' + index }
                                                                value={ item.code }
                                                                primaryText={ item.label } />
                                                );
                                            }
                                        )

                                    }
                                </SelectField>
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > Service Package </Col>

                            <Col md={ 9 }>
                                <SelectField
                                                floatingLabelText={ "Select Service Package" }
                                                value={ this.props.form.spCode }
                                                onChange={ ( event, value , payload ) => {
                                                    this.props.onChangeForm( 'spCode' , payload );
                                                } }  >
                                    {
                                        this.props.data.servicePackage.map(
                                            ( item , index ) => {
                                                return (
                                                    <MenuItem
                                                                key={ '_company_servicePackage_' + index }
                                                                value={ item.spCode }
                                                                primaryText={ item.spName } />
                                                );
                                            }
                                        )

                                    }
                                </SelectField>
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > Company Status </Col>

                            <Col md={ 9 }>
                                <SelectField
                                                floatingLabelText={ "Select Company Status" }
                                                value={ this.props.form.status }
                                                onChange={ ( event, value , payload ) => {
                                                    this.props.onChangeForm( 'status' , payload );
                                                } } >
                                    {
                                        this.props.data.status.map(
                                            ( item , index ) => {
                                                return (
                                                    <MenuItem
                                                                key={ '_company_status_' + index }
                                                                value={ item.code }
                                                                primaryText={ item.label } />
                                                );
                                            }
                                        )
                                    }
                                </SelectField>
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 3 } > Creation Date </Col>

                            <Col md={ 4 }>
                                <DatePicker
                                                hintText={ "From Date" }
                                                mode={ "landscape" }

                                                onChange={
                                                    ( event , date ) => {
                                                        this.props.onChangeForm( 'creationFromDate' , DateUtil.formatDateTimeStick( DateUtil.objectToDate( date ) ) );
                                                    }
                                                }
                                                formatDate={
                                                    ( date ) => {
                                                            return DateUtil.formatDate( DateUtil.objectToDate( date ) );
                                                    }
                                                } />
                            </Col>

                            <Col md={ 4 }>
                                <DatePicker
                                                hintText={ "End Date" }
                                                mode={ "landscape" }

                                                onChange={
                                                    ( event , date ) => {
                                                        this.props.onChangeForm( 'creationEndDate' , DateUtil.formatDateTimeStick( DateUtil.objectToDate( date ) ) );
                                                    }
                                                }
                                                formatDate={
                                                    ( date ) => {
                                                            return DateUtil.formatDate( DateUtil.objectToDate( date ) );
                                                    }
                                                } />
                            </Col>
                        </Row>
                    </Grid>


                    <Grid fluid>
                        <Row className="show-grid">
                            <Col
                                    md={ 12 }
                                    className={ ' company-step1-textAlign-center ' } >

                                <RaisedButton
                                                style={ { flex: 1 } }
                                                label={ "Search" }
                                                secondary={ true }
                                                onClick={ ( evt ) => {
                                                    this.props.onClickSearch( );
                                                } } />

                            </Col>
                        </Row>
                    </Grid>


                </div>


                <hr style={ { marginBottom: 0 } }/>


                <Table
                        fixedHeader={ false }
                        tableLayout="auto">

                    <TableHeader
                                adjustForCheckbox={ false }
                                displaySelectAll={ false }>

                            <TableRow >
                                <TableHeaderColumn> Company ID </TableHeaderColumn>
                                <TableHeaderColumn> CIF Number </TableHeaderColumn>
                                <TableHeaderColumn> Business Name </TableHeaderColumn>
                                <TableHeaderColumn> Business Register Number </TableHeaderColumn>
                                <TableHeaderColumn> Service Package </TableHeaderColumn>
                                <TableHeaderColumn> Status </TableHeaderColumn>
                                <TableHeaderColumn> Date Created </TableHeaderColumn>
                                <TableHeaderColumn> Date Terminated </TableHeaderColumn>
                                <TableHeaderColumn> Reason </TableHeaderColumn>
                            </TableRow>

                    </TableHeader>

                    <TableBody
                                displayRowCheckbox={ false }
                                showRowHover={ true }>

                        {
                                this.props.result.search.map( ( item , index ) => {

                                    return (
                                        <TableRow key={ '_company_' + item.companyId } >

                                            <TableRowColumn>{ item.companyId }</TableRowColumn>
                                            <TableRowColumn>
                                                <a
                                                        href={ 'javascript:void( 0 )' }
                                                        onClick={ () => {
                                                            this.props.onClickDetail( item.cifNumber );
                                                        } } >
                                                    { item.cifNumber }
                                                </a>

                                            </TableRowColumn>
                                            <TableRowColumn>{ item.bussName }</TableRowColumn>
                                            <TableRowColumn>{ item.bussRegNumber }</TableRowColumn>
                                            <TableRowColumn>{ item.spCode }</TableRowColumn>
                                            <TableRowColumn>{ item.status }</TableRowColumn>
                                            <TableRowColumn>{ item.creationDate }</TableRowColumn>
                                            <TableRowColumn>{ item.terminationDate }</TableRowColumn>
                                            <TableRowColumn>{ item.reason }</TableRowColumn>

                                        </TableRow>

                                    )

                                } )
                        }

                    </TableBody>

                </Table>

            </div>
        )
    }
}



