import React from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import {
    Step
    , Stepper
    , StepLabel
}                                                   from 'material-ui/Stepper';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import moment                                       from 'moment';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionCompany , { ReduxEnumCompany }    from '../../../_redux/action/reduxAction.company';








/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';
import HelperVerifyAuthorize                        from '../../_helper/h.verifyAuthorize';

import { DateUtil }                                 from '../../../_util/util.date';






/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';






/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';
import ComponentCustomAlert                         from '../../_component/alert/cc.alert';

import ComponentCompanyStep1                        from './step1/c.company.step1';
import ComponentCompanyStep2                        from './step2/c.company.step2';
import ComponentCompanyStep3                        from './step3/c.company.step3';
import ComponentCompanyStep4                        from './step4/c.company.step4';





import './s.company.info.css';


export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxCompany' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumCompany.INIT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompany.SEARCH_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSearchDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompany.DETAIL_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInfoDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];



                        } else if ( ReduxEnumCompany.UPDATE_PREVIEW_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdatePreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompany.UPDATE_CONFIRM_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompany.ERROR == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestError';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];


                        }


                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;
        }
) (

    class ComponentCompanyInfo extends ComponentAbstract {







        constructor( props ) {
            super( props )


            this.state = {
                step                : 0

                , isError           : false
                , errorMsg          : ''

                , isLoading         : false

                , data              : {
                    servicePackage          : [ ]

                    , status                : [ ]

                    , smeType               : [
                        { code : 'YES' , label : 'YES' }
                        , { code : 'NO' , label : 'NO' }
                    ]

                    , manageMode            : [
                        { code : 'BM' , label : 'Bank-Manage' }
                        , { code : 'SM' , label : 'Self-Manage' }
                    ]

                    , approvalCondition     : [
                        { code : 'AA' , label : 'Anyone to Approve' }
                    ]

                    , role                  : []
                }

                , result            : {
                    search          : []

                    , selected      : {}

                    , info          : {}

                    , preview       : {}

                    , confirm       : {}
                }

                , form              : {
                    companyId                   : ''
                    , cifNumber                 : ''
                    , bussName                  : ''
                    , bussRegNumber             : ''
                    , userId                    : ''
                    , spCode                    : ''
                    , smeType                   : ''
                    , status                    : ''
                    , creationFromDate          : null
                    , creationEndDate           : null
                }

            }

        }











        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    listener
            ################################################################################################################################################
            ################################################################################################################################################
        */

        _onReady() {
            console.log( 'ComponentCompanyList : _onReady : *** ');

            this.requestInit();
        }












        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    event
            ################################################################################################################################################
            ################################################################################################################################################
        */

        onClickViewComp( recordId ) {
            console.log( 'ComponentCompanyList : onClickViewDetail : *** : id => ', recordId );

            let record = {};
            for ( let i = 0; i < this.state.result.search.length; i ++ ) {
                let tmpRecord = this.state.result.search[ i ];

                if ( tmpRecord.cifNumber == recordId ) {
                    record = tmpRecord;
                }
            }

            let result = this.state.result;
            result[ 'selected' ] = record;
            this.setState( {
                result : result
            } , () => {
                this.requestInfo();
            } );
        }











        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    request
            ################################################################################################################################################
            ################################################################################################################################################
        */

        requestInit( ) {
            console.log( 'ComponentCompanyList : requestInit : ***' )

            this.setState(
                { isLoading: false }
                , () => {

                    HelperVerifyAuthorize.verify()
                        .then(
                            () => {

                                Promise.all( [
                                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                ] ).then(
                                    ( value ) => {

                                        this.dispatch(
                                            ReduxActionCompany.rInit(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , { }
                                            )
                                        )

                                    }
                                )

                            }
                        ).catch (
                            ( error ) => {
                                this.requestError( error );
                            }
                        ) ;

                }
            );

        }
        requestInitDone( result ) {
            console.log( 'ComponentCompanyList : requestInitDone : *** : result => ', result );

            let data = this.state.data;
            data[ 'status' ]            = result.status;
            data[ 'servicePackage' ]    = result.servicePackage;
            this.setState( {
                isLoading   : false
                , data      : data
            } );
            this.dispatch( ReduxActionCompany.rRequestEnd() );
        }


        requestSearch( ) {
            console.log( 'ComponentCompanyList : requestSearch : ***' )

            this.setState(
                { isLoading: true }
                , () => {

                    HelperVerifyAuthorize.verify()
                        .then(
                            () => {

                                Promise.all( [
                                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                ] ).then(
                                    ( value ) => {

                                        this.dispatch(
                                            ReduxActionCompany.rSearch(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , {
                                                    companyId       : this.state.form.companyId
                                                    , cifNumber     : this.state.form.cifNumber
                                                    , bussName      : this.state.form.bussName
                                                    , bussRegNumber : this.state.form.bussRegNumber
                                                    , userId        : this.state.form.userId
                                                    , spCode        : this.state.form.spCode
                                                    , billingCode   : this.state.form.billingCode
                                                    , status        : this.state.form.status
                                                    , creationDate  : this.state.form.creationDate
                                                }
                                            )
                                        )

                                    }
                                )

                            }
                        ).catch (
                            ( error ) => {
                                this.requestError( error );
                            }
                        ) ;

                }
            );

        }
        requestSearchDone( result ) {
            console.log( 'ComponentCompanyList : requestSearchDone : *** : result => ', result );

            let resultObj = this.state.result;
            resultObj[ 'search' ] = result;
            this.setState( {
                isLoading: false
                , result : resultObj
            } );
            this.dispatch(
                ReduxActionCompany.rRequestEnd()
            );
        }


        requestInfo( ) {
            console.log( 'ComponentCompanyList : requestInfo : ***' )

            this.setState(
                { isLoading: true }
                , () => {

                    HelperVerifyAuthorize.verify()
                        .then(
                            () => {

                                Promise.all( [
                                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                ] ).then(
                                    ( value ) => {

                                        this.dispatch(
                                            ReduxActionCompany.rDetail(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , {  }
                                            )
                                        )

                                    }
                                )

                            }
                        ).catch (
                            ( error ) => {
                                this.requestError( error );
                            }
                        ) ;

                }
            );
        }
        requestInfoDone( result ) {
            console.log( 'ComponentCompanyList : requestInfoDone : *** : result => ', result );

            let resultObj = this.state.result;
            resultObj[ 'info' ] = result;
            this.setState( {
                step        : 1

                , isLoading     : false
                , result        : resultObj
            } );
            this.dispatch(
                ReduxActionCompany.rRequestEnd()
            );
        }


        requestUpdatePreview( ) {
            console.log( 'ComponentCompanyList : requestUpdatePreview : ***' )

            this.setState(
                { isLoading: true }
                , () => {

                    HelperVerifyAuthorize.verify()
                        .then(
                            () => {

                                Promise.all( [
                                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                ] ).then(
                                    ( value ) => {


                                    this.dispatch(
                                        ReduxActionCompany.rUpdatePreview(
                                            { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                            , {  }
                                        )
                                    )

                                    }
                                )

                            }
                        ).catch (
                            ( error ) => {
                                this.requestError( error );
                            }
                        ) ;

                }
            );

        }
        requestUpdatePreviewDone( result ) {
            console.log( 'ComponentCompanyList : requestUpdatePreviewDone : *** : result => ', result );

            let resultObj = this.state.result;
            resultObj[ 'preview' ] = result;
            this.setState( {
                step            : 2

                , isLoading     : false
                , result        : resultObj
            } );
            this.dispatch(
                ReduxActionCompany.rRequestEnd()
            );
        }


        requestUpdateConfirm( ) {
            console.log( 'ComponentCompanyList : requestUpdateConfirm : ***' )

            this.setState(
                { isLoading: true }
                , () => {

                    HelperVerifyAuthorize.verify()
                        .then(
                            () => {

                                Promise.all( [
                                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                ] ).then(
                                    ( value ) => {

                                        this.dispatch(
                                            ReduxActionCompany.rUpdateConfirm(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , {  }
                                            )
                                        )

                                    }
                                )

                            }
                        ).catch (
                            ( error ) => {
                                this.requestError( error );
                            }
                        ) ;

                }
            );
        }
        requestUpdateConfirmDone( result ) {
            console.log( 'ComponentCompanyList : requestUpdateConfirmDone : *** : result => ', result );

            let resultObj = this.state.result;
            resultObj[ 'confirm' ] = result;
            this.setState( {
                step            : 3

                , isLoading     : false
                , result        : resultObj
            } );
            this.dispatch(
                ReduxActionCompany.rRequestEnd()
            );
        }



        requestError( result ) {
            console.log( 'ComponentCompanyList : requestError : *** : result => ' , result );

            this.setState(
                { isLoading : false }
                , () => {

                    this.handlerException(
                        result
                        , () => {
                            this.setState( {
                                isError         : true
                                , errorMsg      : result.msg
                            } );
                        }
                    );

                }
            )

            this.dispatch(
                ReduxActionCompany.rRequestEnd( )
            );
        }
















        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    render
            ################################################################################################################################################
            ################################################################################################################################################
        */

        render() {
            return (
                <div>

                    <Paper >

                        <h4 style={ { paddingLeft: 25, paddingTop: 12 } }>
                            Company Profile Maintenance
                        </h4>


                        <hr/>

                        <div
                                className={ ' companyInfo-margin-auto companyInfo-paddingTop-20 companyInfo-paddingBottom-20  companyInfo-maxWidth-900 ' }>

                            <Stepper activeStep={ this.state.step }>

                                <Step>
                                    <StepLabel> Search Company </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Update Company Info </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Preview </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Confirmation </StepLabel>
                                </Step>

                            </Stepper>

                        </div>

                        <div>
                            {
                                ( () => {

                                    switch ( this.state.step ) {

                                        case 0:
                                            return (
                                                <ComponentCompanyStep1
                                                            form={ this.state.form }
                                                            formValidation={ {} }

                                                            data={ this.state.data }
                                                            result={ this.state.result }


                                                            onChangeForm={
                                                                ( key , value ) => {
                                                                    let form = this.state.form;
                                                                    form[ key ] = value;
                                                                    this.setState( { form : form } );
                                                                }
                                                            }

                                                            onClickSearch={ () => {
                                                                this.requestSearch();
                                                            } }
                                                            onClickDetail={ ( cifNumber ) => {
                                                                this.onClickViewComp( cifNumber );
                                                            } }
                                                />
                                            );
                                            break;

                                        case 1:
                                            return (
                                                <ComponentCompanyStep2
                                                            form={ this.state.form }
                                                            formValidation={ {} }

                                                            data={ this.state.data }
                                                            result={ this.state.result }


                                                            onChangeForm={
                                                                ( key , value ) => {
                                                                    let result = this.state.result;
                                                                    result[ 'info' ][ key ] = value;
                                                                    this.setState( { result : result } );
                                                                }
                                                            }

                                                            onClickSubmit={ () => {
                                                                this.requestUpdatePreview();
                                                            } }
                                                />
                                            );
                                            break;

                                        case 2:
                                            return (
                                                <ComponentCompanyStep3
                                                            form={ this.state.form }
                                                            formValidation={ {} }

                                                            data={ this.state.data }
                                                            result={ this.state.result }


                                                            onChangeForm={
                                                                ( key , value ) => {
                                                                    let result = this.state.result;
                                                                    result[ 'info' ][ key ] = value;
                                                                    this.setState( { result : result } );
                                                                }
                                                            }

                                                            onClickSubmit={ () => {
                                                                this.requestUpdateConfirm();
                                                            } }
                                                />
                                            );
                                            break;

                                        case 3:
                                            return (
                                                <ComponentCompanyStep4
                                                            form={ this.state.form }
                                                            formValidation={ {} }

                                                            data={ this.state.data }
                                                            result={ this.state.result }

                                                            onClickSubmit={ () => {
                                                            } }
                                                />
                                            );
                                            break;

                                        default:
                                            return ( null );

                                    }

                                } )()
                            }
                        </div>


                    </Paper>

                   <ComponentCustomLoading
                        visible={ this.state.isLoading }
                    />


                    <ComponentCustomAlert
                        visible={ this.state.isError }

                        title={ 'Error' }
                        msg={ this.state.errorMsg }

                        onClose={ () => {
                            this.setState( {  isError : false } );
                        } }

                        alertType={ 'error' }
                    />


                </div>
            )
        }
    }



)



