import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import SwipeableViews                               from 'react-swipeable-views';







/* ################################################### */
/*          helper         */
/* ################################################### */

import { DateUtil }                                 from '../../../../_util/util.date';
import { NumberUtil }                               from '../../../../_util/util.number';







/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';




import './s.company.step2.css';




export default class ComponentCompanyStep2 extends ComponentAbstract {



    static propTypes = {
        form                : PropTypes.object
        , formValidation    : PropTypes.object

        , data              : PropTypes.object

        , result            : PropTypes.object

        , onChangeForm      : PropTypes.func

        , onClickSubmit     : PropTypes.func

    }


    static defaultProps = {
        form                : {}
        , formValidation    : {}
        , data              : {}

        , result            : {}

        , onChangeForm      : () => {}

        , onClickSubmit     : () => {}
    }







    constructor( props ) {
        super( props )


        this.state = {
            slideIndex : 0
        }

    }











    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {
        return (
            <div>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Service Package </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.spCode }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Category Type </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.categoryType }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Company ID </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.companyId }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Company Name </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.bussName }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Address </Col>
                        <Col md={ 9 }>
                            {
                                this.props.result.info.addresses.map( ( address ) => {

                                    return (
                                        <div key={ address.address1 }>

                                            <div
                                                className={ ' company-profile-form-marginBottom-10 ' }
                                                style={ { fontWeight : 'bolder', flex: 2 } }>
                                                    { address.address1 } <br/>
                                                    { address.address2 } <br/>
                                                    { address.address3 } <br/>
                                                    { address.city } <br/>
                                                    { address.postcode } <br/>
                                                    { address.statecode }
                                            </div>

                                        </div>

                                    )

                                } )
                            }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Contact Info </Col>
                        <Col md={ 9 }>
                            {
                               this.props.result.info.contactInfo.map( ( contact ) => {
                                    return (
                                        <div key={ contact.type }>

                                            <div
                                                className={ ' company-profile-form-marginBottom-10 ' }
                                                style={ { fontWeight : 'bolder', flex: 2 } }>
                                                    { contact.type }
                                                    <br/>
                                                    { contact.number }

                                            </div>

                                        </div>

                                    )
                                } )
                            }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Email </Col>
                        <Col md={ 9 }>
                            {
                                this.props.result.info.emails.map( ( email ) => {
                                    return (
                                        <div key={ email.type }>
                                            <div
                                                className={ ' company-profile-form-marginBottom-10 ' }
                                                style={ { fontWeight : 'bolder', flex: 2 } }>
                                                    { email.type }
                                                    <br/>
                                                    { email.emailAddress }
                                            </div>

                                        </div>

                                    )
                                } )
                            }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Branch </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.homeBranch }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > RM Code </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.rmCode }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > RM Lead Code </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.rmLeadCode }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Manage Mode </Col>
                        <Col md={ 9 }>

                            <RadioButtonGroup
                                                name="servicePackage"
                                                valueSelected={ this.props.result.info.manageMode }
                                                onChange={ ( event , value ) => {
                                                    this.props.onChangeForm( 'manageMode' , value );
                                                } } >
                                {
                                    this.props.data.manageMode.map(
                                        ( item , index ) => {
                                            return (
                                                <RadioButton
                                                            key={ '_company_info_manageMode_' + index }
                                                            value={ item[ 'code' ] }
                                                            label={ item[ 'label' ] }
                                                />
                                            );
                                        }
                                    )
                                }
                            </RadioButtonGroup>

                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Corporate Online Daily Limit </Col>
                        <Col md={ 9 }>

                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "Corporate Online Daily Limit" }
                                        value={ this.props.result.info.transactionLimit }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'transactionLimit' , newValue );
                                        } } />

                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Transaction Management </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.transactionManagement }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Approval Condition </Col>
                        <Col md={ 9 }>

                            <SelectField
                                            floatingLabelText={ "Approval Condition" }
                                            value={ this.props.result.info.approvalCondition }
                                            onChange={ ( event, value , payload ) => {
                                                this.props.onChangeForm( 'approvalCondition' , payload );
                                            } }  >
                                {
                                    this.props.data.approvalCondition.map(
                                        ( item , index ) => {
                                            return (
                                                <MenuItem
                                                            key={ '_company_smeType_' + index }
                                                            value={ item.code }
                                                            primaryText={ item.label } />
                                            );
                                        }
                                    )
                                }
                            </SelectField>

                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Transaction Workflow </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.transactionWorkflow }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > SME Type </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.smeType }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Company Status </Col>
                        <Col md={ 9 }>
                            <SelectField
                                            floatingLabelText={ "Company Status" }
                                            value={ this.props.result.info.status }
                                            onChange={ ( event, value , payload ) => {
                                                this.props.onChangeForm( 'status' , payload );
                                            } }  >
                                {
                                    this.props.data.status.map(
                                        ( item , index ) => {
                                            return (
                                                <MenuItem
                                                            key={ '_company_status_' + index }
                                                            value={ item.code }
                                                            primaryText={ item.label } />
                                            );
                                        }
                                    )
                                }
                            </SelectField>
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 3 } > Designated Account Number </Col>
                        <Col md={ 9 }>
                            { this.props.result.info.status }
                        </Col>
                    </Row>
                </Grid>



                <Grid fluid>
                    <Row className="show-grid">
                        <Col
                                md={ 12 }
                                className={ ' company-step2-textAlign-center company-step2-padding-10 ' } >

                            <RaisedButton
                                            style={ { flex: 1 } }
                                            label={ "Submit" }
                                            secondary={ true }
                                            onClick={ ( evt ) => {
                                                this.props.onClickSubmit( );
                                            } } />

                        </Col>
                    </Row>
                </Grid>

            </div>
        )
    }
}



